
import {Component, OnInit, ViewChild} from '@angular/core';
import {MatPaginator} from '@angular/material/paginator';
import {MatSort} from '@angular/material/sort';
import {MatTableDataSource} from '@angular/material/table';
import { ChargerService } from 'app/shared/services/charger/charger.service';
import { ActionDialogComponent } from '../action-dialog/action-dialog.component';
import { MatDialog,MatDialogRef } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { MessageConstant} from 'app/shared/utils/message.constant';
import { MatTableFilter } from 'mat-table-filter';
import {ViewEncapsulation} from '@angular/core';
import { fuseAnimations } from '@fuse/animations';
import {FuseConfirmDialogComponent} from '@fuse/components/confirm-dialog/confirm-dialog.component';
import * as XLSX from 'xlsx';
import { ViewDialogComponent } from '../view-dialog/view-dialog.component';
import { jsPDF } from 'jspdf';
import 'jspdf-autotable';
import { ClientService } from 'app/shared/services/client/client.service';
import { ChargingStationService } from 'app/shared/services/charging-station/charging-station.service';
import { CpoService } from 'app/shared/services/cpo/cpo.service';
import { map, startWith } from 'rxjs/operators';
import { Validators, FormBuilder, FormGroup } from '@angular/forms';
import { Observable } from 'rxjs';
import {SelectionModel} from '@angular/cdk/collections';

export class Client {
  id: string;
  name: string;
  description: string;
  address: string;
  logoPath: string;
  mobile: string;
  email: string;
  status: string;
  created_date: string;
  createdby: number;
  modify_date: string;
  modifyby: number;
  statusString:string;
  serial_no : string;
  client_name:string;
  model_name:string;
}

/**
 * @title Data table with sorting, pagination, and filtering.
 */
@Component({
  selector: 'app-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.scss'],
  encapsulation: ViewEncapsulation.None,
  animations   : fuseAnimations,
})
export class ListComponent implements OnInit {
  filterEntity: Client;
  filterType: MatTableFilter;
  displayedColumns: string[] = ['select', 'client_name', 'name','serial_no','model_name','status','action'];
  head = [['Client Name','Name','Serial Number', 'Model Name','Status']]
  dataSource: MatTableDataSource<Client>;
  selection = new SelectionModel<Client>(true, []);
  localStorage : any;
  //clients : Client[];
  allChargers :any;
  @ViewChild(MatPaginator, {static: true}) paginator: MatPaginator;
  @ViewChild(MatSort, {static: true}) sort: MatSort;
  confirmDialogRef: MatDialogRef<FuseConfirmDialogComponent>;
  dialogRef: any;
  clientSearch = false;
  nameSearch = false;
  serialSearch = false;
  modelSearch = false;
  statusSearch = false;
  exportedData: any[];
  form: FormGroup;
  cposName: any;
  clientName:any;
  localData: any;
  chargingStationName: any;
  filteredOptionsCharClient:Observable<any[]>;
  filteredOptionsCharCPO: Observable<any[]>;
  filteredOptionsCharStat: Observable<any[]>;
  clientCpoStaDetails: any;
  clientList: any;
  cpoList: any;
  stationList: any;
  station: any;
  constructor(private chargerService : ChargerService,public dialog : MatDialog,private snackBar : MatSnackBar,private fb: FormBuilder,
    private chargingStationService: ChargingStationService,private clientService:ClientService,private cpoService: CpoService,) {
   
      this.form = this.fb.group({
        charClient:[''],
        charCPO: [''],
        charStat: ['']
      });
  }

  ngOnInit() {
    
    this.localStorage = JSON.parse( localStorage.getItem('userDetails'));
    this.clientCpoStaDetails = JSON.parse(localStorage.getItem('clientCpoStaDetails'));
    //this.getAllClientChargers(this.localStorage.userId);
    this.filterEntity = new Client();
    this.filterType = MatTableFilter.ANYWHERE;
    this.getClients();
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  getAllClientChargers(id){
    this.chargerService.getAllClientChargers(id).subscribe(
      (data:any)=>{
        if(data != null){
          this.allChargers = data;
          for(var i=0; i < this.allChargers.length;i++){
            if(this.allChargers[i].status == 'N'){
              this.allChargers[i].statusString = 'Inactive';
            }else{
              this.allChargers[i].statusString = 'Active';
            }
          }
        this.dataSource = new MatTableDataSource(this.allChargers);
        this.dataSource.paginator = this.paginator;
        this.dataSource.sort = this.sort;
        }else{
          this.snackBar.open(MessageConstant.NoRecordFoundMessage, 'Dismiss',{duration : 3000 })
          return;
        }
      },(err)=>{
      }
    )
  }

  delete(id){
    this.confirmDialogRef = this.dialog.open(FuseConfirmDialogComponent, {
      disableClose: true
  });
  //this.confirmDialogRef.componentInstance.confirmMessage = 'Are you sure you want to delete?';
  this.confirmDialogRef.afterClosed().subscribe(result => {
      if ( result )
      {
        this.chargerService.deleteChargerFromClient(id,this.localStorage.userId).subscribe(
          (data:any)=>{
            
            this.getAllClientChargers(this.localStorage.userId);
            this.snackBar.open(MessageConstant.deleteMessage,'Dismiss',{duration:2000});
          },(err)=>{
            
          }
        )
      }
      this.confirmDialogRef = null;
  });
   
  }

  openDialog(action,data): void {
    
    let params = {
      action : action,
      data : data
    }

    const dialogRef = this.dialog.open(ActionDialogComponent, {
      width: '800px',
      data: params,
      autoFocus: false,
      disableClose: true,
      panelClass: 'app-action-dialog',   
    });

    dialogRef.afterClosed().subscribe(result => {
      
      // this.moderate(result);
      this.getAllClientChargers(this.localStorage.userId);
    });
  }

  changeVisibility(visibleOption,ev){
    // visibleOption = !visibleOption;
    //nameSearch = false;
      //descSearch = false;
      ev.stopPropagation();
    if(visibleOption == 'clientSearch'){
      this.clientSearch = !this.clientSearch;
      this.filterEntity.client_name= "";
    }
    
    if(visibleOption == 'nameSearch'){
      this.nameSearch = !this.nameSearch;
      this.filterEntity.name= "";
    }
    if(visibleOption == 'serialSearch'){
      this.serialSearch = !this.serialSearch;
      this.filterEntity.serial_no= "";
    }
    if(visibleOption == 'modelSearch'){
      this.modelSearch = !this.modelSearch;
      this.filterEntity.model_name= "";
    }
  
    if(visibleOption == 'statusSearch'){
      this.statusSearch = !this.statusSearch;
      this.filterEntity.statusString= "";
    }
   }

   tableExport(){
     this.exportedData = [];
    for (let i = 0; i < this.dataSource.filteredData.length; i++) {
       var chargerModel =this.dataSource.filteredData[i];
      //this.exportedData.push({name: `name${i}`, description: `description${i}`,
      //statusString: `statusString${i}`});
      var data = {'Client Name':chargerModel.client_name,'Name':chargerModel.name,'Serial No':chargerModel.serial_no,
                  'Model Name': chargerModel.model_name,'Status':chargerModel.statusString};
      this.exportedData.push(data);
    }
     const workSheet = XLSX.utils.json_to_sheet(this.exportedData);
       const workBook: XLSX.WorkBook = XLSX.utils.book_new();
       
      XLSX.utils.book_append_sheet(workBook, workSheet, 'sales disptch');
       XLSX.writeFile(workBook, 'Sales Dispatch.xlsx');
   }

   viewDialog(action,data) : void{
    let params = {
      action : action,
      data : data
    }
    const dialogRef = this.dialog.open(ViewDialogComponent, {
      width: '800px',
      data: params,
      panelClass: 'app-action-dialog',
      autoFocus: false,
      disableClose: true 
    });
    dialogRef.afterClosed().subscribe(result => {
      // this.moderate(result);
      this.allChargers();
    });
  }

  pdfExport(){
    var doc = new jsPDF({ // set needed dimensions for any elemen 
    });
    this.exportedData = [];
    for (let i = 0; i < this.dataSource.filteredData.length; i++) {
      var chrger = this.dataSource.filteredData[i];
     //this.exportedData.push({name: `name${i}`, description: `description${i}`,
     //statusString: `statusString${i}`});
     var data =[chrger.client_name,chrger.name,chrger.name,chrger.serial_no,chrger.mobile,chrger.model_name,chrger.statusString] ;
     
     this.exportedData.push(data);
   }
   var img = new Image();
   var imgRight = new Image();
    var src = 'assets/images/logos/logo_sparkev_main.png';
    var srcSparkEV = 'assets/images/logos/logo_SparkEV_black.png';
   imgRight.src =srcSparkEV;
   var createdDate = new Date().toLocaleString(('en-GB'));
   img.src = src;
   doc.setFontSize(11);
   doc.setTextColor(100);
   doc.setFontSize(18);
    (doc as any).autoTable({
      head: this.head,
      body: this.exportedData,
      theme: 'grid',
      headStyles: { fontStyle: 'majalla',fillColor:[11, 148, 68]},
      styles: {
        cellPadding: 1,
        fontSize: 8,
      },
      margin: { top: 22 },
      didDrawCell: data => {
      },
      didDrawPage:data=>{
        doc.addImage(img , 15, 7, 15, 6);
        doc.addImage(imgRight, 170, 7, 25, 6);
        
        doc.text('Product Dispatch Report' ,doc.internal.pageSize.getWidth() / 2, 11, { align: 'center'});
        
        doc.setTextColor(211,211,211);
        doc.setFontSize(60);
        doc.setGState( doc.GState({opacity: 0.6}));
        doc.text('Confidential/Not for Public use',25,285,null,60, );
        doc.setGState( doc.GState({opacity: 1}));
        doc.setTextColor(100);
        doc.setFontSize(9);
        doc.text('Date:'+ createdDate ,15, 18);
        
        doc.text('Generated By:'+ this.localStorage.f_Name ,195, 18,{ align: 'right'});
        doc = this.addWaterMark(doc);
      }
    })
  
    // below line for Open PDF document in new tab
    doc.output('dataurlnewwindow')
   
    // below line for Download PDF document  
    doc.save('Product Dispatch Report.pdf');
   
  }
  addWaterMark(doc){
    var newpage = doc.getNumberOfPages();
    const i = doc.internal.getNumberOfPages();
    doc.setTextColor(100);
        doc.setFontSize(10);
     //Print Page 1 of 4 for example
     
     doc.text('Page ' +  String(i) + ' of ' + String(newpage),210-20,297-10,null,null,"right")
    return doc;
  
  }

  getClients() {
    this.clientList = JSON.parse(localStorage.getItem('ClientList'));
    //console.log(this.clientList)
    if (this.localStorage.role_code != "SA") {
      if (this.clientList != null && this.clientList != undefined) {
        const toSelect = this.clientList[0];
        this.form.get('charClient').setValue(toSelect);
        this.getCPOByClient(toSelect.client_id);
      }
      this.filteredOptionsCharClient = this.form.controls.charClient.valueChanges
        .pipe(
          startWith(''),
          map(value => typeof value === 'string' ? value : value.client_name),
          map(name => name ? this._filterCharClient(name) : this.clientList)
        );

    } else {
      this.clientService.getActiveClientsCW(this.localStorage.userId).subscribe(
        (resp: any) => {
          this.clientName = resp;
          const toSelect = this.clientName.find(c => c.id == this.localStorage.client_id);
          if (toSelect != null) {
            this.form.get('charClient').setValue(toSelect);
            this.getCPOByClient(toSelect.id);
          }
          this.filteredOptionsCharClient = this.form.controls.charClient.valueChanges
            .pipe(
              startWith(''),
              map(value => typeof value === 'string' ? value : value.name),
              map(name => name ? this._filterCharClient(name) : this.clientName)
            );
        }, (err) => { }
      );
    }
  }
  onClientChange(client_id){
    this.cpoList = [];
    this.stationList = [];
    this.form.get('charCPO').setValue("");
    this.form.get('charStat').setValue("");
    this.getCPOByClient(client_id);
  }

  getCPOByClient(client_id) {
    this.cpoList = JSON.parse(localStorage.getItem('CpoList'));
    //console.log(this.cpoList)
    if (this.localStorage.role_code != "SA") {
      if (this.cpoList != null && this.cpoList != undefined) {
        const toSelect = this.cpoList[0];
        this.form.get('charCPO').setValue(toSelect);
        this.getStationByCPO(toSelect.cpo_id);
      }
      this.filteredOptionsCharCPO = this.form.controls.charCPO.valueChanges
        .pipe(
          startWith(''),
          map(value => typeof value === 'string' ? value : value.cpo_name),
          map(name => name ? this._filterCharCPO(name) : this.cpoList)
        );
    } else {
      this.cpoService.getCpoByClientId(client_id).subscribe(
        (resp: any) => {
          this.cposName = resp;
          const toSelect = this.cposName[0];
          if (toSelect != null) {
            this.form.get('charCPO').setValue(toSelect);
            this.getStationByCPO(toSelect.id);
          }
          this.filteredOptionsCharCPO = this.form.controls.charCPO.valueChanges
            .pipe(
              startWith(''),
              map(value => typeof value === 'string' ? value : value.name),
              map(name => name ? this._filterCharCPO(name) : this.cposName)
            );
        }, (err) => { }
      );
    }
  }
  
  onCPOChange(event) {
    this.stationList = [];
    this.form.get('charStat').setValue("");
    this.getStationByCPO(event);
  }

  getStationByCPO(id) {
    this.stationList = JSON.parse(localStorage.getItem('StationList'));
    //console.log(this.stationList)
    this.station =[];

    if (this.localStorage.role_code != "SA") {
      for(var i = 0; i< this.stationList.length; i++){
        if(id == this.stationList[i].cpo_id){
          this.station.push( this.stationList[i])
        }
      }
      if (this.station != null && this.station != undefined) {
        const toSelect = this.station[0];//this.station.find(c => c.cpo_id == id);
        this.form.get('charStat').setValue(toSelect);
        this.getAllClientChargers(this.localStorage.userId);
      }
      this.filteredOptionsCharStat = this.form.controls.charStat.valueChanges
        .pipe(
          startWith(''),
          map(value => typeof value === 'string' ? value : value.station_name),
          map(station_name => station_name ? this._filterCharStat(station_name) : this.station)
        );
    } else {
      this.chargingStationService.getChargingStationByCpoId(id).subscribe(
        (resp: any) => {
          this.chargingStationName = resp.data;
          const toSelect = this.chargingStationName[0];
          this.form.get('charStat').setValue(toSelect);
          this.getAllClientChargers(this.localStorage.userId);
          this.filteredOptionsCharStat = this.form.controls.charStat.valueChanges
            .pipe(
              startWith(''),
              map(value => typeof value === 'string' ? value : value.name),
              map(name => name ? this._filterCharStat(name) : this.chargingStationName)
            );
        }, (err) => { }
      )
    }

  }
  onStationChange(event) {
    this.getAllClientChargers(this.localStorage.userId);
    //this.getAllClientChargers(event);
  }

  filteredOptions() {
    this.filteredOptionsCharClient = this.form.controls.charClient.valueChanges
      .pipe(
        startWith(''),
        map(value => typeof value === 'string' ? value : value.name),
        map(name => name ? this._filterCharClient(name) : this.clientName)
      );

    this.filteredOptionsCharCPO = this.form.controls.charCPO.valueChanges
      .pipe(
        startWith(''),
        map(value => typeof value === 'string' ? value : value.name),
        map(name => name ? this._filterCharCPO(name) : this.cposName)
      );

    this.filteredOptionsCharStat = this.form.controls.charStat.valueChanges
      .pipe(
        startWith(''),
        map(value => typeof value === 'string' ? value : value.name),
        map(name => name ? this._filterCharStat(name) : this.chargingStationName)
      );
  }

  displayCharClient(item: any): string {
    return item && item.name ? item.name : '';
  }

  displayCharCPO(item: any): string {
    return item && item.name ? item.name : '';
  }
  displayCharStat(item: any): string {
    return item && item.name ? item.name : '';
  }
  displayClient(item: any): string {
    return item && item.client_name ? item.client_name : '';
  }
  displayCPO(item: any): string {
    return item && item.cpo_name ? item.cpo_name : '';
  }
  displayStat(item: any): string {
    return item && item.station_name ? item.station_name : '';
  }

  private _filterCharClient(name: string): any[] {
    if (this.localStorage.role_code != "SA") {
      this.form.get('charCPO').setValue('');
      this.form.get('charStat').setValue('');
      const filterValue = name.toLowerCase();
      return this.clientList.filter(option => option.client_name.toLowerCase().includes(filterValue));
    } else {
      this.form.get('charCPO').setValue('');
      this.form.get('charStat').setValue('');
      const filterValue = name.toLowerCase();
      return this.clientName.filter(option => option.name.toLowerCase().includes(filterValue));
    }
  }

  private _filterCharCPO(name: string): any[] {
    if (this.localStorage.role_code != "SA") {
      this.form.get('charStat').setValue('');
      const filterValue = name.toLowerCase();
      return this.cpoList.filter(option => option.cpo_name.toLowerCase().includes(filterValue));
    } else {
      this.form.get('charStat').setValue('');
      const filterValue = name.toLowerCase();
      return this.cposName.filter(option => option.name.toLowerCase().includes(filterValue));
    }
  }

  private _filterCharStat(name: string): any[] {
    if (this.localStorage.role_code != "SA") {
      const filterValue = name.toLowerCase();
      return this.station.filter(option => option.station_name.toLowerCase().includes(filterValue));
    } else {
      const filterValue = name.toLowerCase();
      return this.chargingStationName.filter(option => option.name.toLowerCase().includes(filterValue));
    }
  }

  isAllSelected() {
    const numSelected = this.selection.selected.length;
    const numRows = this.dataSource.data.length;
    return numSelected === numRows;
  }

  /** Selects all rows if they are not all selected; otherwise clear selection. */
  masterToggle() {
    if (this.isAllSelected()) {
      this.selection.clear();
      return;
    }

    this.selection.select(...this.dataSource.data);
  }

  /** The label for the checkbox on the passed row */
  checkboxLabel(row?: Client): string {
    if (!row) {
      return `${this.isAllSelected() ? 'deselect' : 'select'} all`;
    }
    return `${this.selection.isSelected(row) ? 'deselect' : 'select'} row ${row.name + 1}`;
  }
  
}

