@Component({
  selector: 'app-create',
  templateUrl: './create.component.html',
  styleUrls: ['./create.component.scss'],
  encapsulation: ViewEncapsulation.None,
  animations   : fuseAnimations,
  providers:[DatePipe]
})
export class CreateComponent implements OnInit {
 

  form: FormGroup;
  brands:any;
  models:any;
  chargerTypes:any;
  vehicleTypes:any;
  localStorage : any;
  filteredOptionCountry: Observable<any[]>;
  filteredOptionState: Observable<any[]>;
  filteredOptionCity: Observable<any[]>;
  countries:any;
  stateName:any;
  cities:any;

  states:any;
  pin_code_id:any;
  country_id:any;
  state_id:any;
  @ViewChild("toggleElement") ref: any;
  @ViewChild("toggleChargerAsElement") chargerAsRef: any;
  status = false;
  chargerAs = false;
  UserInputType = [
    { "name": "Table View", ID: "D1"},
    { "name": "Upload CSV", ID: "D2"}
]
filteredOptionsClient: Observable<any[]>;
clients:any;
selectedInputType = this.UserInputType[0].name;
public unitValue;
selectedUnitValue : any;
totalSerial =[];
serialNumber: FormArray;
oldUnitCount:number;
chargerSerials:any;
chargerSerialsOrg:any;
filteredOptionSerial: Observable<any[]>;
dataSource : MatTableDataSource<SerialData>;
displayedColumns: string[] = ['serial_no'];
mappedColumns: string[] = ['serial_no','remarks'];
mappedDataSource :MatTableDataSource<SerialData>;
unmappedColumns: string[] = ['serial_no','remarks'];
unmappedDataSource :MatTableDataSource<SerialData>;
excelSerial= [];
//@ViewChild(MatPaginator, {static: true}) paginator: MatPaginator;
// @ViewChild('serialTable', { static: false }) serialTable: ElementRef;
// @ViewChild('mapped_table') mapped_table: ElementRef;
// @ViewChild('unmapped_table') unmapped_table: ElementRef;
importFile: File;
maxDate = new Date();
  uploadExcelTable:boolean =false;
  importSerialNo: SerialData[] = [];
  unitType:any;
   selectedChargerSerial  = [];
   public chipSelectedSerialNo: Charger[] = [];
   public filteredSerial: Observable<String[]>;
   private allowFreeTextAddEngineer = false;

 
  readonly separatorKeysCodes: number[] = [ENTER, COMMA];
  
  @ViewChild('serialInput') serialInput: ElementRef<HTMLInputElement>;
  @ViewChild('auto') matAutocomplete: MatAutocomplete;
   validSerialNo = [];
  selectedSerialNo = [];
  response_mapped =[];
  response_unmapped = [];
  returnDataStatus :boolean = false;
  removable = true;
   @ViewChild('importPaginator') importPaginator: MatPaginator;
   @ViewChild('mappedPaginator') mappedPaginator: MatPaginator;
   @ViewChild('unmappedPaginator') unmappedPaginator: MatPaginator;
constructor(private fb: FormBuilder, private masterService : MasterService,private excelSrv: ExcelService,
    private clientService : ClientService,private snackBar: MatSnackBar,private chargerService:ChargerService,
    private router : Router,public countryService: CountriesService,private datePipe: DatePipe,private cdr :ChangeDetectorRef) {
     
    
  }

  ngOnInit() {
    this.initialize();
  }

  initialize(){

    this.localStorage = JSON.parse( localStorage.getItem('userDetails'));

    this.form = this.fb.group({
      client: ['', [autocompleteObjectValidator(),Validators.required]],
      serialNumber: [''],
      dispatch_date:['',[Validators.required]],
      status : this.status == true ? 'Y' : 'N',
      chargerAs : this.chargerAs == true ? 0 : 1      
    });
    
    //this.getCountry();
    this.getClients();
    this.getSerialNo();
    //this.getAllCity();
  }
  // ngAfterViewInit() {
  //   this.dataSource.paginator = this.paginator
  //   this.mappedDataSource.paginator = this.mappedPaginator;
  //   this.unmappedDataSource.paginator = this.unmappedPaginator;
  // }
  createSerial(): FormGroup {
    return this.fb.group({
      serialNo: ['', [Validators.required,autocompleteObjectValidator()]],
     
    });
  }
  getSerialNo(){
    this.chargerService.getPlantChargers().subscribe(
      (data:any)=>{
        if(data != null){
          this.chargerSerials = data;
          this.chargerSerialsOrg = [...this.chargerSerials];
          this.filteredOptionSerial = this.form.controls.serialNumber.valueChanges
          .pipe(
            startWith(''),
            map(value => typeof value === 'string' ? value : value.serial_no),
            map(serial_no => serial_no ? this._filterSerial(serial_no) : this.chargerSerials)
          );
        }
       
      },(err)=>{
        
      }
    )
  }

  getClients(){
    
    this.clientService.getActiveClientsCW(this.localStorage.userId).subscribe(
      (data:any)=>{
        if(data != null){
          this.clients = data;
          this.filteredOptionsClient = this.form.controls.client.valueChanges
          .pipe(
            startWith(''),
            map(value => typeof value === 'string' ? value : value.name),
            map(name => name ? this._filterClientModels(name) : this.clients)
          );
        }
       
      },(err)=>{
        
      }
    )

  }
  displaySerial(item: any): string {
    return item && item.serial_no ? item.serial_no : '';
  }
  private _filterSerial(name: string): any[] {
    const filterValue = name.toLowerCase();
    this.chargerSerials = this.resetChrgerList();
   
    
    
    return this.chargerSerials.filter(option => option.serial_no.toLowerCase().includes(filterValue));
  }

  resetChrgerList():any[]{
    var chargerSerials = [];
    if ( this.selectedSerialNo != undefined && this.selectedSerialNo.length > 0) {
      
      var isExist = true;
     
      for(var k =0; k< this.chargerSerialsOrg.length;k++){
          var serialNo = this.chargerSerialsOrg[k];
           isExist = false;
           for(var i =0;i< this.selectedSerialNo.length; i++){
               var selectedSerialNo = this.selectedSerialNo[i];
               if(selectedSerialNo.serial_no == serialNo.serial_no){
                  isExist =true;
               }
           }

           if(!isExist){
            chargerSerials.push(serialNo);
           }
      }

  }else{
    chargerSerials= [...this.chargerSerialsOrg];
  }
   return chargerSerials;
  }

  displayClient(item: any): string {
    return item && item.name ? item.name : '';
  }
  private _filterClientModels(name: string): any[] {
    const filterValue = name.toLowerCase();
    return this.clients.filter(option => option.name.toLowerCase().includes(filterValue));
  }
  onSerialSelected(event)
  {
    this.selectedSerialNo.push(event.option.value);
    //this.form.controls['serialNumber'].setValue('');
    this.form.get('serialNumber').setValue('');
    this.chargerSerials = this.resetChrgerList();
    this.unitType += 1;
    this.filteredOptionSerial = this.form.controls.serialNumber.valueChanges
        .pipe(
          startWith(''),
          map(value => typeof value === 'string' ? value : value.serial_no),
          map(serial_no => serial_no ? this._filterSerial(serial_no) : this.chargerSerials)
        );
  }
  onSubmit(formDirective){
    if(this.form.valid){
      let formValues = this.form.value;
      if(this.selectedSerialNo.length == 0 ){
        this.snackBar.open(MessageConstant.serialBoxEmpty, 'Dismiss', { duration: 5000 });
      }else{
        let params = {
          client_id :formValues.client.id, 
          sub_client_id:"12",
          dispatch_status:"Y",
          dispatch_date: this.datePipe.transform(formValues.dispatch_date,"yyyy-MM-dd") ,
          charger_data: this.selectedSerialNo.map(v=>({id:v.id,serial_no:v.serial_no})),
          status :  this.status == true ? 'Y' : 'N', 
          is_private:this.chargerAs == true? 1:0,
          created_by : this.localStorage.userId, 
          dispatch_by:this.localStorage.userId
        }
    
        this.chargerService.getProductDispatch(params).subscribe(
          (data:any )=>{
            if(data.status = true){
              this.returnDataStatus = true;
              this.selectedSerialNo = [];
              this.getClients();
              this.response_mapped = data.data[0].chargers_mapped;
              this.response_unmapped = data.data[0].chargers_not_mapped;
              formDirective.resetForm();
              this.form.reset();
              this.form.markAsPristine();
            }
            if (this.response_mapped.length<0){
            }else{
                 this.cdr.detectChanges();
                  this.mappedDataSource = new MatTableDataSource(this.response_mapped);
                this.mappedDataSource.paginator = this.mappedPaginator;
            }
            if (this.response_unmapped.length<0){
            }else{
              this.cdr.detectChanges();
                  this.unmappedDataSource = new MatTableDataSource(this.response_unmapped);
                  this.unmappedDataSource.paginator = this.unmappedPaginator;
            }
            this.snackBar.open(MessageConstant.salesDispatch, 'Dismiss', { duration: 5000 });
          },
          (err)=>{
            this.snackBar.open(MessageConstant.errorMessage, 'Dismiss', { duration: 5000 });
          }
        )
      }
      
    }else{
      return;
    }
     
  
   
    
  }
 
  
  
 
  uploadedFile(evt: any) {
    let target: DataTransfer = <DataTransfer>(evt.target);
    if (target.files.length !== 1) throw new Error('Cannot use multiple files');
    this.selectedSerialNo  = [];
    let reader: FileReader = new FileReader();
    reader.onload = (e: any) => {
   
      let bstr: string = e.target.result;
      let data = <any[]>this.excelSrv.importFromFile(bstr);

      let header: string[] = Object.getOwnPropertyNames(new SerialData());
      let importedData = data.slice(1);

      this.importSerialNo = importedData.map(arr => {
        let obj = {};
        for (let i = 0; i < header.length; i++) {
          const k = header[i];
          obj[k] = arr[i];
        }
        return <SerialData>obj;
      })
     
     // let tempSerialArray = [];
     
    //  this.importSerialNo.map(element => {
    //   element.Tag = "Unverified";
    //  });
        for(var i= 0;i< this.importSerialNo.length;i++){
        for(var j= 0;j<this.chargerSerials.length;j++ ){
              
          if(this.importSerialNo[i].serial_no == this.chargerSerials[j].serial_no)
          {
            
            this.selectedSerialNo.push(this.chargerSerials[j])
          }
        }
          
      }
        // this.selectedSerialNo.push(this.importSerialNo);
      this.uploadExcelTable = true;
      this.cdr.detectChanges();
      this.dataSource = new MatTableDataSource(this.selectedSerialNo);
      this.dataSource.paginator = this.importPaginator;
    }
   
    
    reader.readAsBinaryString(target.files[0]);

  }
  

  getErrorMessage(form: FormGroup) {
    return form.get('email').hasError('required')
      ? 'You must enter a value'
      : form.get('email').hasError('email')
      ? 'Not a valid email'
      : '';
  }
 
  updateStatus(event){
    this.status = event.checked;
    this.ref.checked = this.status;
  } 
  updateChargerAsStatus(event){
    this.chargerAs = event.checked;
    this.chargerAsRef.checked = this.chargerAs;
  }
  
 
  public removeSerial(i): void {
    this.selectedSerialNo.splice(i,1);
    this.chargerSerials = this.resetChrgerList();
    this.unitType -1 ;
    this.filteredOptionSerial = this.form.controls.serialNumber.valueChanges
        .pipe(
          startWith(''),
          map(value => typeof value === 'string' ? value : value.serial_no),
          map(serial_no => serial_no ? this._filterSerial(serial_no) : this.chargerSerials)
        );
  }
  selectionMade(event: Event, trigger: MatAutocompleteTrigger) {
    event.stopPropagation();
    trigger.openPanel();
}
mappedTableExport()
{
   const workSheet = XLSX.utils.json_to_sheet(this.mappedDataSource.data);
    const workBook: XLSX.WorkBook = XLSX.utils.book_new();
    XLSX.utils.book_append_sheet(workBook, workSheet, 'SheetName');
    XLSX.writeFile(workBook, 'Mapped Charger.xlsx');

}
unmappedTableExport()
{
  const workSheet = XLSX.utils.json_to_sheet(this.unmappedDataSource.data);
  const workBook: XLSX.WorkBook = XLSX.utils.book_new();
  XLSX.utils.book_append_sheet(workBook, workSheet, 'SheetName');
  XLSX.writeFile(workBook, 'UnMapped Charger.xlsx');
}
backForm(){
  //this.returnDataStatus= false;
}
}


