import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ClientSalesDispatchCrudComponent } from './client-sales-dispatch-crud/client-sales-dispatch-crud.component';
import { TABS_DEMO_ROUTES } from './client-sales-dispatch-crud/routes';
import { AuthGuard } from 'app/shared/interceptors/auth-gaurd';
const routes: Routes = [
    {
        path: 'crud', component: ClientSalesDispatchCrudComponent, canActivate: [AuthGuard], children: TABS_DEMO_ROUTES
    },

]

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})

export class ClientSalesDispatchRoutingModule { }